<script>
  import { onMount, afterUpdate } from 'svelte';
  import Sidebar from './Sidebar/Sidebar';
  import Toolbar from './Toolbar';
  import ui from 'stores/UI';
  import options from 'stores/Options';
  import draft from 'stores/Draft';
  import WeaveDisplay from './WeaveDisplay/WeaveDisplay';
  import Selection from './Selection/SelectionComponent';
  import RulerBar from './RulerBar';
  import InfoBar from './InfoBar';
  import Dialog from 'pages/Editor/Sidebar/Dialog';

  import { vec2 } from 'gl-matrix';

  let infobarContainer;
  let scrollContainer;
  let canvasContainer;
  let canvasWidth;
  let canvasHeight;
  let cellSizeInput = $ui.cellSize;
  let hasChangedCellSize;
  let scrollPercentage = [];

  let infobarHeight = 0;
  let toolbarHeight = 0;
  let scrollbarWidth = 15;
  let scrollbarHeight = 15;
  let weaveDisplay;

  let resizeObserver;

  $: warpCount = $draft.warpCount;
  $: weftCount = $draft.weftCount;
  $: cellSize = $ui.cellSize / window.devicePixelRatio;
  $: width = warpCount * cellSize;
  $: height = weftCount * cellSize;
  $: showThreading = $options.showThreading;
  $: showTreadleOrder = $options.showTreadleOrder;
  $: selectedMenu = $options.selectedMenu;
  $: disableScroll = $options.disableScroll;

  let sidebarOpen = true;

  afterUpdate(() => {
    if (hasChangedCellSize) {
      hasChangedCellSize = false;
      let [scrollLeftMax, scrollTopMax] = getBounds();
      let x = (1.0 - scrollPercentage[0]) * scrollLeftMax;
      let y = (1.0 - scrollPercentage[1]) * scrollTopMax;
      scrollContainer.scrollTo(x, y);
    }
  });

  onMount(() => {
    let dpr = window.devicePixelRatio;
    scrollbarWidth = scrollContainer.offsetWidth - scrollContainer.clientWidth;
    scrollbarHeight =
      scrollContainer.offsetHeight - scrollContainer.clientHeight;
    infobarHeight = infobarContainer.clientHeight;

    let scrollLeftMax =
      scrollContainer.scrollWidth - scrollContainer.clientWidth;
    let scrollTopMax =
      scrollContainer.scrollHeight - scrollContainer.clientHeight;
    scrollContainer.scrollTo(scrollLeftMax, scrollTopMax);
    resizeObserver = new ResizeObserver(() => {
      if (weaveDisplay !== undefined && weaveDisplay !== null) {
        weaveDisplay.syncCanvasDimensions();
      }
    });
    resizeObserver.observe(canvasContainer);

    scrollContainer.addEventListener('contextmenu', (e) => {
      e.preventDefault();
      return false;
    });

    scrollContainer.addEventListener('wheel', (event) => {
      if (event.ctrlKey) {
        event.preventDefault();
        cellSizeInput += event.deltaY * -0.03;
        cellSizeInput = Math.max(5, Math.min(70, cellSizeInput));
        changeCellSize();
      }
    });

    let startScale;
    scrollContainer.addEventListener('touchstart', (e) => {
      let touches = e.targetTouches;
      if (touches.length === 2) {
        let t0 = touches[0];
        let t1 = touches[1];
        let p0 = vec2.fromValues(t0.clientX, t0.clientY);
        let p1 = vec2.fromValues(t1.clientX, t1.clientY);
        startScale = vec2.length(vec2.sub(vec2.create(), p0, p1));
      }
    });

    scrollContainer.addEventListener('touchmove', (e) => {
      let touches = e.targetTouches;
      if (touches.length === 2) {
        let t0 = touches[0];
        let t1 = touches[1];
        let p0 = vec2.fromValues(t0.clientX, t0.clientY);
        let p1 = vec2.fromValues(t1.clientX, t1.clientY);
        let newScale = vec2.length(vec2.sub(vec2.create(), p0, p1));
        let scaleDiff = newScale / startScale;
        cellSizeInput *= scaleDiff;
        changeCellSize();
        startScale = newScale;
      }
    });

    scrollContainer.addEventListener('touchend', (e) => {
      if (e.touches.length === 0 && startScale !== undefined) {
        ui.update((draft) => {
          draft.cellSize = draft.cellSize;
        });
        startScale = undefined;
      }
    });

    let rect = canvasContainer.getBoundingClientRect();
    canvasWidth = rect.width;
    canvasHeight = rect.height;
  });

  function getBounds() {
    let dpr = window.devicePixelRatio;
    let scrollLeftMax =
      scrollContainer.scrollWidth - scrollContainer.clientWidth;
    let scrollTopMax =
      scrollContainer.scrollHeight - scrollContainer.clientHeight;
    return [scrollLeftMax * dpr, scrollTopMax * dpr];
  }

  function getPosition() {
    let [scrollLeftMax, scrollTopMax] = getBounds();
    let dpr = window.devicePixelRatio;
    let x = scrollLeftMax - scrollContainer.scrollLeft * dpr;
    let y = scrollTopMax - scrollContainer.scrollTop * dpr;
    return [x, y];
  }

  function updatePosition() {
    let pos = getPosition();
    ui.update((draft) => {
      draft.scrollX = pos[0];
      draft.scrollY = pos[1];
    });
  }

  function changeCellSize() {
    let newCellSize = cellSizeInput;
    let oldCellSize = $ui.cellSize;
    if (newCellSize !== oldCellSize) {
      hasChangedCellSize = true;
      let [x, y] = getPosition();
      let [scrollLeftMax, scrollTopMax] = getBounds();
      scrollPercentage = [x / scrollLeftMax, y / scrollTopMax];
      ui.update((draft) => {
        draft.cellSize = newCellSize;
      });
    }
  }
</script>

<Toolbar bind:height={toolbarHeight} bind:sidebarOpen />
<Sidebar bind:open={sidebarOpen} />
<div
  on:scroll={updatePosition}
  bind:this={scrollContainer}
  class="container"
  tabindex="0"
>
  {#if selectedMenu !== -1}
    <Dialog />
  {/if}
  <div
    class="scrollpane"
    style={`left: ${width * 1}px; top: ${height * 1}px;`}
  />
  <div
    bind:this={canvasContainer}
    class="fixed"
    style={`
    top: ${toolbarHeight}px;
    right: ${scrollbarWidth}px;
    bottom: ${scrollbarWidth + infobarHeight}px
    `}
  >
    <div class="weave-container">
      {#if scrollContainer !== undefined}
        <WeaveDisplay bind:this={weaveDisplay} bind:scrollContainer />
        <Selection {weaveDisplay} />
        {#if $ui.showThreading}
          <RulerBar
            width={canvasWidth * window.devicePixelRatio}
            stepCount={$draft.warpCount}
            distance={$ui.xStepDistance}
            position={[$draft.treadleCount + 4, 2]}
          />
        {/if}
        {#if $ui.showTreadleOrder}
          <RulerBar
            height={canvasHeight * window.devicePixelRatio}
            stepCount={$draft.weftCount}
            distance={$ui.yStepDistance}
            position={[2, $draft.shaftCount + 4]}
          />
        {/if}
      {/if}
    </div>
  </div>
  <div
    class="infobarContainer-container"
    bind:this={infobarContainer}
    style={`bottom: ${scrollbarHeight}px; right: ${scrollbarWidth}px`}
  >
    <InfoBar />
  </div>
</div>

<style>
  .container {
    overflow: auto;
    --left-menu-width: 0px;
  }

  .infobarContainer-container {
    position: absolute;
    bottom: 0;
    left: var(--left-menu-width);
  }
  .fixed {
    display: flex;
    justify-content: stretch;
    align-items: stretch;
    flex-direction: column;

    position: absolute;
    right: 0;
    left: var(--left-menu-width);
    bottom: 0;
    top: 0;

    pointer-events: none;
  }

  .scrollpane {
    background-color: #5959308f;
    pointer-events: none;
    position: relative;
    width: 10px;
    height: 10px;
  }

  .weave-container {
    position: relative;
    width: 100%;
    height: 100%;
  }
</style>
