<script>
  import { afterUpdate } from 'svelte';
  import YarnSettings from './YarnSettings';
  import NumberInput from 'components/NumberInput/NumberInput';
  import draft from 'stores/Draft';
  import ui from 'stores/UI';
  import * as draftUtil from 'util/DraftUtil';
  import { t } from 'precompile-intl-runtime';

  let shaftCount = $draft.shaftCount;
  let treadleCount = $draft.treadleCount;
  let warpCount = $draft.warpCount;
  let weftCount = $draft.weftCount;

  function submit(event) {
    event.preventDefault();
    draft.update((temp) => {
      if ($draft.shaftCount !== shaftCount) {
        draftUtil.updateShaftCount(temp, Number(shaftCount));
      }
      if ($draft.treadleCount !== treadleCount) {
        draftUtil.updateTreadleCount(temp, Number(treadleCount));
      }
      if ($draft.warpCount !== warpCount) {
        draftUtil.updateWarpCount(temp, Number(warpCount));
      }
      if ($draft.weftCount !== weftCount) {
        draftUtil.updateWeftCount(temp, Number(weftCount));
      }
    }, false);
  }
</script>

<form on:submit={submit}>
  <NumberInput
    label={$t('page.weave_settings.shaft_count')}
    bind:value={shaftCount}
    showArrows={true}
  />
  <NumberInput
    label={$t('page.weave_settings.treadle_count')}
    bind:value={treadleCount}
    showArrows={true}
  />
  <NumberInput
    label={$t('page.weave_settings.warp_count')}
    bind:value={warpCount}
  />
  <NumberInput
    label={$t('page.weave_settings.weft_count')}
    bind:value={weftCount}
  />
  <button type="submit">Submit</button>
</form>

<h2>Yarns</h2>
<YarnSettings />

<style>
  form {
    display: flex;
    flex-direction: column;
  }

  form > * {
    margin: 8px 4px;
  }
</style>
