<script>
  import { fade } from 'svelte/transition';
  import { t } from 'precompile-intl-runtime';
  import Subcomponents from './Subcomponents';
  import HelpIcon from 'icons/help.svg';

  import ui from '../../../stores/UI';
  import SaveButton from 'components/SaveButton/SaveButton';
  import draft from '../../../stores/Draft';

  $: selectedMenu = $ui.selectedMenu;
  let sidebar;
  export let open = false;

  function selectMenu(index) {
    console.log('select menu', index);
    open = false;
    ui.update((draft) => {
      draft.selectedMenu = draft.selectedMenu === index ? -1 : index;
    });
  }
  function importFromFile(e) {
    let files = e.target.files;
    if (files.length > 0) {
      let file = files[0];
      let reader = new FileReader();
      reader.onload = (readFile) => {
        try {
          let newDraft = JSON.parse(readFile.target.result);
          draft.set(newDraft);
        } catch (e) {
          console.error(e);
        }
      };
      reader.readAsText(file);
    }
  }
</script>

<div class="sidebar-container">
  {#if open}
    <div
      class="sidebar"
      bind:this={sidebar}
      transition:fade={{
        x: -150,
        duration: 20,
        opacity: 1,
      }}
    >
      {#each Subcomponents as item, i}
        <button class="icon-button" on:click={() => selectMenu(i)}>
          <div class={'icon'}>
            {@html item.icon}
          </div>
          <span>{$t(`page.${item.name}.title`)}</span>
        </button>
      {/each}
      <SaveButton />

      <div class="save-load">
        <input
          type="file"
          name="upload"
          id="upload"
          on:input={importFromFile}
        />
        <label class="icon-button button" for="upload"
          >{$t('terms.upload')}</label
        >
      </div>
      <div class="help">
        <a href={`/weaver/docs_${$ui.locale}`}>
          <div class="icon">
            {@html HelpIcon}
          </div>
        </a>
      </div>
    </div>
  {/if}
</div>

